<template>
    <div>
        <b-navbar :toggleable="false" :type="mode" :variant="mode">
            <b-navbar-brand href="/" v-text="trans('tasbih')"></b-navbar-brand>
            <b-collapse id="nav-collapse" is-nav class="flex-row-reverse">
                <b-navbar-nav>
                    <b-nav-item @click.prevent="changeMode('light')"><b-icon icon="sun"></b-icon></b-nav-item>
                    <b-nav-item @click.prevent="changeMode('dark')"><b-icon icon="moon"></b-icon></b-nav-item>
                </b-navbar-nav>
            </b-collapse>
        </b-navbar>
    </div>
</template>


<script>
    export default {
        computed: {
            mode() {
                return this.$store.getters.settings.mode
            }
        },
        methods: {
            changeMode(value) {
                this.$emit('mode', value)
            }
        }
    }
</script>