import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

axios.defaults.baseURL =  process.env.NODE_ENV === 'production' ? '/tasbih/' : '/';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    settings : JSON.parse(localStorage.getItem('settings')) || {mode:'light'},
    posts : JSON.parse(localStorage.getItem('posts')) || {},
    post : {}
  },
  mutations: {
    set_mood(state, data){
      state.settings.mode = data;
      localStorage.setItem('settings', JSON.stringify(state.settings));
    },
    posts_request() {},
    posts_success(state, data){
      state.posts = data;
      localStorage.setItem('posts', JSON.stringify(state.posts));
    },
    posts_error() {},
    post_request() {},
    post_success(state, data){
      state.post = data;
    },
    post_error() {}
  },
  actions: {
    change_mode({commit}, data){
      commit('set_mood', data);
    },
    fetch_posts({commit}){
      return new Promise((resolve, reject) => {
        commit('posts_request');
        axios({url: '/data/posts.json', method: 'GET' })
            .then(res => {
              commit('posts_success', res.data);
              resolve(res);
            })
            .catch(err => {
              commit('posts_error');
              reject(err);
            })
      })
    },
    fetch_post({commit}, data){
      return new Promise((resolve, reject) => {
        commit('post_request');
        axios({url: `/data/${data}.json`, method: 'GET' })
            .then(res => {
              commit('post_success', res.data);
              resolve(res);
            })
            .catch(err => {
              commit('post_error');
              reject(err);
            })
      })
    },
  },
  getters: {
    settings (state) {
      return state.settings
    },
    posts (state) {
      return state.posts
    },
    post (state) {
      return state.post
    }
  },
  modules: {
  }
})
