<template>
  <div id="app" :class="[mode]">
    <nav-bar @mode="changeMode"></nav-bar>
    <b-container>
      <router-view/>
    </b-container>
  </div>
</template>

<script>
  import NavBar from './components/NavBar'

  export default {
    components: {
      NavBar
    },
    computed: {
      mode() {
        return this.$store.getters.settings.mode
      }
    },
    methods: {
      changeMode(value) {
        this.$store.dispatch('change_mode', value)
      }
    }
  }
</script>