import Vue from 'vue'
import * as _ from "lodash";
import lang from "@/assets/lang/fa"

Vue.mixin({
    methods: {
        persianNumber(value) {
            if (_.isString(value) || _.isInteger(value)) {
                value = value.toString();
                value = value
                    .replace(new RegExp('0', 'g'), '۰')
                    .replace(new RegExp('1', 'g'), '۱')
                    .replace(new RegExp('2', 'g'), '۲')
                    .replace(new RegExp('3', 'g'), '۳')
                    .replace(new RegExp('4', 'g'), '۴')
                    .replace(new RegExp('5', 'g'), '۵')
                    .replace(new RegExp('6', 'g'), '۶')
                    .replace(new RegExp('7', 'g'), '۷')
                    .replace(new RegExp('8', 'g'), '۸')
                    .replace(new RegExp('9', 'g'), '۹');

                return value;
            }
            return '';
        },
        trans(value, args) {
            let string = _.get(lang, value);

            _.eachRight(args, (val, key) => {
                string = _.replace(string, `{${key}}`, val);
            });
            return string;
        },
    }
});