<template>
    <div class="context">
        <b-row align-v="center" class="mt-2">
            <b-col class="name text-left">
                <span v-text="post.name"></span>
            </b-col>
            <b-col cols="auto" class="text-center ">
                <b-button pill block :variant="sliding ? 'info' : 'outline-info'" class="btn-counter" v-text="counter"></b-button>
            </b-col>
        </b-row>
        <div class="vh-50">
            <b-carousel id="carousel"
                        v-model="slide"
                        :interval="0"
                        @sliding-start="sliding = true"
                        @sliding-end="sliding = false">
                <template v-for="step in post.steps">
                    <template v-for="item in range(step.count)">

                        <b-carousel-slide img-blank>
                            <template v-slot:img>
                                <b-row class="text-center mt-2 mb-2 pre-line">
                                    <b-col cols="12" class="description" v-text="step.description"></b-col>
                                </b-row>
                                <b-list-group class="list-group-striped p-0">
                                    <template v-for="phrase in step.phrases">
                                        <b-list-group-item class="text-center phrase">{{ phrase }}</b-list-group-item>
                                    </template>
                                </b-list-group>
                            </template>
                        </b-carousel-slide>

                    </template>
                </template>
            </b-carousel>
        </div>
        <b-row  align-v="center" class="justify-content-center text-center m-2">
            <b-col cols="auto">
                <b-button pill variant="success" class="btn-next" @click.prevent="slide += 1">
                    <b-icon icon="chevron-double-right" font-scale="1.5"></b-icon>
                </b-button>
            </b-col>
            <b-col cols="auto">
                <b-button pill variant="outline-secondary" class="btn-previous" @click.prevent="slide -= 1">
                    <b-icon icon="chevron-double-left" font-scale="1.5"></b-icon>
                </b-button>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import * as _ from 'lodash';

    export default {
        name: 'Post',
        data() {
            return {
                slide: 0,
                sliding: null,
            }
        },
        computed: {
            post() {
                return this.$store.getters.post
            },
            counter() {
                let position = this.slide + 1;
                let current = {};
                for(let index in this.post.steps) {
                    if(position <= this.post.steps[index].count) {
                        current = this.post.steps[index];
                        break;
                    }
                    else {
                        position -= this.post.steps[index].count;
                    }
                }

                if(current.count == undefined) {
                    return `...`
                }

                return this.persianNumber(`${position}   از   ${current.count}`)
            }
        },
        methods: {
            range(count) {
                return _.range(count);
            }
        },
        created() {
            this.$store.dispatch('fetch_post', this.$route.query.id);
        }
    }
</script>

<style scoped>
    .btn-next {
        height: 50px;
        width: 50px;
    }
    .btn-previous {
        height: 50px;
        width: 50px;
    }
    .vh-50 {
        height: 65vh !important
    }
</style>